  public projectsList = [];

  constructor(public el: ElementRef) {}

  @HostListener("window:scroll", [])
  checkScroll() {
    const componentPosition = this.el.nativeElement.offsetTop - 500;
    const scrollPosition = window.pageYOffset;
    if (scrollPosition >= componentPosition && !this.startAnim) {
      this.startAnim = true;
    }
  }

  ngOnInit() {
    this.projectsList = this.projects;
    this.generateTagFilterList();
  }

  public seeMoreProjects() {
    this.showHideProjects = !this.showHideProjects;

    if (!this.showHideProjects) {
      let el = document.getElementById("third-project");
      el.scrollIntoView({ behavior: "smooth", block: "start" });
    }

    setTimeout(() => {
      this.showHideProjects2 = !this.showHideProjects2;
    }, 500);
  }

  // Pushear item si no existe
  pushItem(tag: string, array: string[]) {
    let exists = false;

    array.forEach((e) => {
      if (e == tag) {
        exists = true;
      }
    });

    if (!exists) {
      array.push(tag);
    }
  }

  // Recorro todos los tags y los agrego a una lista
  generateTagFilterList() {
    this.projects.forEach((e) => {
      e.tags.forEach((t) => {
        this.pushItem(t, this.filterTags);
      });
    });
    this.filterTags.sort();
  }

  //Genero nueva lista de PROYECTOS con el filtro tag seleccionado
  generateListProjectTagFilter(tag: string) {
    if (this.filterSelected == tag) {
      this.projectsList = this.projects;
      this.filterSelected = "";
    } else {
      this.filterSelected = tag;
      this.projectsList = [];
      this.projects.forEach((e) => {
        e.tags.forEach((t) => {
          if (t == tag) {
            this.projectsList.push(e);
          }
        });
      });
    }
  }

  // Cerrar modal
  closeModal() {
    this.showModal = !this.showModal;
  }
