import { Component, OnInit, HostListener, ElementRef } from '@angular/core';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss']
})
export class ContactComponent implements OnInit {
  public mailCopied = false;
  public phoneCopied = false;
  public startAnim = false;

  constructor(
    public el: ElementRef
  ) { }

  @HostListener('window:scroll', [])
  checkScroll() {
    const componentPosition = (this.el.nativeElement.offsetTop) - 500;
    const scrollPosition = window.pageYOffset;
    if (scrollPosition >= componentPosition && !this.startAnim) {
      this.startAnim = true;
    }
  }

  ngOnInit() {
  }

  public copyText(val: string) {
    let selBox = document.createElement('textarea');
    selBox.style.position = 'fixed';
    selBox.style.left = '0';
    selBox.style.top = '0';
    selBox.style.opacity = '0';
    selBox.value = val;
    document.body.appendChild(selBox);
    selBox.focus();
    selBox.select();
    document.execCommand('copy');
    document.body.removeChild(selBox);
  };

  public showCopiedMail() {
    this.mailCopied = true;

    setTimeout(() => {
      this.mailCopied = false;
    }, 1500);
  }

  public showCopiedPhone() {
    this.phoneCopied = true;

    setTimeout(() => {
      this.phoneCopied = false;
    }, 1500);
  }

  public scroll(id) {
    let el = document.getElementById(id);
    el.scrollIntoView({ behavior: "smooth", block: "start" });
  }
}
